@use '../../styles/variables' as *;
@use 'sass:color';

.accordion {
  border: 1px solid $gray-300;
  border-radius: 0.5rem;
  overflow: hidden;
  background-color: $gray-50;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.2);

  &.mobile {
    font-size: 0.9rem;
    width: 730px;

    .accordion-header {
      width: 96%;
    }
  }

  &.web {
    font-size: 1rem;
  }

  &.expanded {
    border-color: color.adjust($gray-300, $lightness: -10%);
  }

  .accordion-header {
    all: unset;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 97%;
    padding: 1rem;
    background-color: $gray-100;
    cursor: pointer;

    .title {
      font-weight: bold;
      line-height: 1.2;
    }

    .icon-wrapper {
      background-color: #1d4ed8;
      border-radius: 9999px;
      padding: 0.3rem;
      display: flex;
      align-items: center;
      justify-content: center;

      .icon {
        fill: white;
      }
    }
  }

  .accordion-content {
    background-color: white;
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: max-height 0.1s ease, opacity 0.1s ease;

    &.expanded {
      max-height: 1000px;
      opacity: 1;
    }

    &.collapsed {
      max-height: 0;
      opacity: 0;
    }

    .accordion-slot-wrapper ::slotted(.accordion-item) {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0.75rem 1rem;
      background-color: $gray-200;
      border-bottom: 1px solid $gray-300;
      color: inherit;
      text-decoration: none;
      transition: background-color 0.2s ease;

      &:hover {
        background-color: color.adjust($gray-200, $lightness: -4%);
      }

      span {
        margin-left: 16px;
      }

      .icon {
        margin-left: 0.5rem;
        width: 1.2rem;
        height: 1.2rem;
      }
    }
  }
}